}

#js-menu-circle {
  z-index: -1;
}
#js-menu-circle.is-active, #js-menu-circle.is-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 2;
}
#js-menu-circle.is-active::before, #js-menu-circle.is-close::before {
  display: block;
  position: absolute;
  content: "";
  top: 16px;
  right: 20px;
  width: 36px;
  height: 36px;
